
import { defineComponent, ref } from 'vue';

export default defineComponent({
  setup() {
    const isGitLogoHovered = ref<boolean>(false);

    function handleGitLogoHover(isHovered: boolean): void {
      isGitLogoHovered.value = isHovered;
    }

    return {
      isGitLogoHovered,
      handleGitLogoHover,
    };
  },
});
